import React, { Component } from 'react';
import './ImageItem.css';
import CategorySelect from './CategorySelect.js';
import {addImage} from './ApiActions.js'

class NewImageItem extends Component 
{

    constructor(props)
    {
        super(props);
        
        this.state = {
            categoryValue : '', 
            infoValue: '',

            endpoint: this.props.endpoint,
            pubkey: this.props.pubkey
        };
        
        this.handleCategoryChange = this.handleCategoryChange.bind(this);
        this.handleInfoChange = this.handleInfoChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);        
    }    
    
    handleCategoryChange(event) {
        this.setState({categoryValue: event.target.value});
    }

    handleInfoChange(event) {
      this.setState({infoValue: event.target.value});
    }

    handleSubmit(event) {
      alert('A name was submitted: ' + this.state.categoryValue);
      var item = {       
        "type": 1,
        "category": this.state.categoryValue,
        "info": this.state.infoValue,
      };
      //console.log("this.state.endpoint: " + this.state.endpoint);
      //console.log("this.state.pubkey: " + this.state.pubkey);
      addImage(this.state.endpoint, item, this.state.pubkey);
      event.preventDefault();
    }

    componentWillReceiveProps(newProps) {
      this.setState({
        endpoint: newProps.endpoint, 
        pubkey: newProps.pubkey
      });
    }

    render() 
    {
  
      return (
        <div className = "image-item">       
          <div className = "image-item-left-div">
            <div className = "image-item-img-div">
                <img 
                    className="image-item-img"
                    src={this.state.infoValue}
                />
            </div>
          </div>

          <form 
              className = "image-item-form"
              onSubmit={this.handleSubmit}
          >
            <label>
              <p className = "image-item-form-field-title">Categoria:</p>
              <CategorySelect
                className = "image-item-category"
                type = "text" 
                name = "category"
                value = { this.state.categoryValue }
                onChange = { this.handleCategoryChange }
              />
            </label>

            <label className = "image-item-label">
              <p className="image-item-form-field-title">Url:</p>
              
              <textarea
                className = "image-item-info"
                type = "text" 
                name = "info"
                onChange = { this.handleInfoChange }
              >
              {this.state.infoValue}
              </textarea>
            </label>

            
            <p className="image-item-form-field-title">
              <input 
                className = "image-item-submit"
                type = "submit" 
                defaultValue = "Aggiungi Nuova" 
              />
            </p>

          </form>

        </div>
      );
    }
  }
  
  export default NewImageItem;


