import React, { Component } from 'react';
import './CategorySelect.css';

class CategorySelect extends Component 
{
    render() 
    {
  
      return(
        <select
            value = {this.props.value}
            onChange = {this.props.onChange}
            className = {this.props.className}
        >
            <option
                className="image-item-category-option"
                value="Generica"
            >
                [00] Generica
            </option>

            <option 
                className="image-item-category-option" 
                value="Buongiorno"
            >
                [01] Buongiorno
            </option>
            
            <option  className="image-item-category-option" value="Amore">[03] Amore</option>
            <option className="image-item-category-option" value="Pensieri">[04] Pensieri</option>
            <option className="image-item-category-option" value="Viaggi">[05] Viaggi</option>
            <option className="image-item-category-option" value="Auguri">[06] Auguri</option>
            <option className="image-item-category-option" value="Animali">[07] Animali</option>
            <option className="image-item-category-option" value="Cattoliche">[08] Cattoliche</option>
            <option className="image-item-category-option" value="Mamma">[09] Mamma</option>
            <option className="image-item-category-option" value="Amicizia">[10] Amicizia</option>
            <option className="image-item-category-option" value="Film">[11] Film</option>
            <option className="image-item-category-option" value="Divertenti">[12] Divertenti</option>
            <option className="image-item-category-option" value="Calcio">[13] Calcio</option>
            <option className="image-item-category-option" value="Settimanali">[14] Settimanali</option>
            <option className="image-item-category-option" value="Buon Appetito">[15] Buon Appetito</option>
            <option className="image-item-category-option" value="Pomeriggio - Sera">[16] Pomeriggio - Sera</option>
            <option className="image-item-category-option" value="Piccanti">[17] Piccanti</option>
        </select>
      );
    }
}

export default CategorySelect;


