import fetch from 'isomorphic-fetch';

export function addImage(endpoint, item, pubkey) {
    var data = {
        "cmd": "add",
        "pubkey": pubkey,
        "item": item
    };
    console.log(data);
    return fetch(endpoint + '/images/' + data.cmd, {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        //mode: 'no-cors',
        body: JSON.stringify(data)
    }).then(res => {
        //console.log(res.text); 
        //var json = res.json();
        //console.log(json);
        return res.body.json();
    }).catch(err => err);
}

export function getImages(endpoint, pubkey) {
    var data = {
        "pubkey": pubkey
    };
    console.log(data);
    return fetch(endpoint + '/images', {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        //mode: 'no-cors',
        body: JSON.stringify(data)
    }).then(res => {
        return res.json();
    }).then(json => {
        return json;
    }).catch(err => err);
}


