import React, { Component } from 'react';
import logo from './AppIcon_50x50.png';
import './App.css';
//import ImageList from 'react-image-list';
import ImageItem from './ImageItem.js';
import NewImageItem from './NewImageItem.js';
import {getImages} from './ApiActions.js'

class App extends Component {

  constructor(props)
  {
    super(props);

    this.state = {
      data: [],
      imagickServiceIP: '',
      apiIP: '',
      apiPubkey: ''
    }

    this.handleImagickServiceIPChange = this.handleImagickServiceIPChange.bind(this);
    this.handleApiIPChange = this.handleApiIPChange.bind(this);
    this.handleApiPubkeyChange = this.handleApiPubkeyChange.bind(this);       
    // this.handleChildProps = this.handleChildProps.bind(this);       
  }

  /*
  handleChildProps(e) {
    e.preventDefault();
    this.setState(prevState => ({
      data: [...prevState.data, newDataItem]
    }))
  }
  */
  handleImagickServiceIPChange(e) {
    this.setState({imagickServiceIP: e.target.value});
  }

  handleApiIPChange(e) {
    this.setState({apiIP: e.target.value});
    getImages(e.target.value, this.state.pubkey).then(images => {
      this.setState({data: images});
    });    
  }

  handleApiPubkeyChange(e) {
    this.setState({apiPubkey: e.target.value});
  }

  componentWillMount()
  {

  }

  render() {

    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Buongiornissimo Image Storage</h1>
        </header>
        <div className = "IPsDiv">
        {/*
          <label>
            <p className = "image-item-form-field-title">Imagick Service IP:</p>
            <input 
              className = "image-item-ip-imagick"
              type = "text" 
              name = "ipImagick"
              value = { this.state.imagickServiceIP }
              onChange = { this.handleImagickServiceIPChange }
            />
          </label>
        */}
          <label>
            <p className = "image-item-form-field-title">API EndPoint:</p>
            <input 
              className = "image-item-ip-api"
              type = "text" 
              name = "ipApi"
              value = { this.state.apiIP }
              onChange = { this.handleApiIPChange }
            />
          </label>
          <label>
            <p className = "image-item-form-field-title">pubkey:</p>
              <input 
                className = "image-item-ip-api"
                type = "text" 
                name = "apiPubkey"
                value = { this.state.apiPubkey }
                onChange = { this.handleApiPubkeyChange }
              />
          </label>
        </div>
        <div className = "NewImageContainer">
          <NewImageItem 
              endpoint = {this.state.apiIP}
              pubkey = {this.state.apiPubkey}
          />
        </div>
        <div className="ImageContainer">                  
          {            
            this.state.data.length > 0 &&
            this.state.data.map((item, index) =>
              <ImageItem 
                key={index}
                id = {item._id}
                category = {item.category}
                info = {item.info}
                status = {item.status}
                src={item.url}
              />              
            )
          }
        </div>

      </div>
    );
  }
}

export default App;



